<template lang="pug">
.assignTimePicker
    div
        select.labelSelector(v-model='semesterLabel' @change='changeLabel')
            option(value='') 請選擇或輸入學期標籤
            option(v-for='option in labelOption' :key='option.label' :value='option.label') {{ option.label }}
    .radius-box
        el-row
            el-col.picker-col-lable.row-border(:span="4")
            el-col.picker-col.row-border(v-for="day in days" :span="4") {{day.label}}
        el-row.picker-row(v-for="(time,i) in times" justify="space-around" :style="!Number.isInteger(time.value)?'border-top: 2px solid #F2A54B':''")
            el-col.picker-col-lable(:span="4") {{time.label}}
            el-col.picker-col.pickable(v-for="(day,j) in days" :span="4" @mouseenter="mouseEnter" @mouseleave="mouseLeave" @mousedown="mouseDown($event,day.value+time.value)" @mouseup="mouseUp" @mousemove="mouseMove($event,day.value+time.value)")
</template>

<script>
import {weektimeMixin} from "@/components/weektimeMixin"
import { computed } from 'vue'
export default {
    mixins: [weektimeMixin],
    props: {
        modelValue: Object,
        default: () => ({
            available_time: '',
            semester_label: ''
        })
    },
    setup(props, { emit }) {
        const theModel = computed({  // Use computed to wrap the object
        get: () => props.modelValue,
        set: (value) => emit('update:modelValue', value),
        });

        return { theModel };
    },
    data () {
        return {
            mousepressed: false,
            mousepop: false,
            selectedTime: [],
            semesterLabel: '',
            labelOption: ''
        }
    },
    watch: {
        modelValue: function(){
            this.createBlock()
        }
    },
    async created() {
        if(this.$store.state.cases.length<=0)
            this.$store.dispatch('fetchCases') 
        await this.$store.dispatch('fetchSemesterLabel')
        this.labelOption = this.$store.state.semester_label.data
    },
    mounted() {
        this.createBlock()
    },
    methods: {
        createBlock() {
            if(this.modelValue) {
                let picked = []
                let modelValue_v2 = this.modelValue.available_time
                this.semesterLabel = this.modelValue.semester_label
                
                try {
                    picked = modelValue_v2.split(',')
                } catch (error) {
                    console.error('Error occurred while parsing modelValue:', error)
                }
                if(picked.length>1){
                    this.clearBlock()
                    this.selectedTime = []
                    for(let i=0;i<picked.length;i++){
                        this.selectedTime.push(picked[i])
                        let aAry = picked[i].split('')
                        aAry[0] = parseInt(this.dayRule[aAry[0]])-1
                        aAry[1] = parseInt(this.timeRule[aAry[1]])-1
                        let element = document.getElementsByClassName("pickable")
                        element[aAry[0]+aAry[1]*5].classList.add('picked')
                    }
                }
            }
        },
        clearBlock() {
            let element = document.getElementsByClassName("pickable")
            for(let i=0;i<element.length;i++){
                element[i].classList.remove('picked')
            }
            this.selectedTime = []
        },
        mouseEnter() {
            // this.mousepop = true
        },
        mouseLeave() {
            this.mousepop = false
        },
        mouseDown(event,value) {
            this.mousepressed = true
            this.handleTime(event,value)
        },
        mouseUp() {
            this.mousepressed = false
        },
        mouseMove(event,value) {
            if(this.mousepressed && !this.mousepop){
                this.handleTime(event,value)
            }
        },
        handleTime(event,value) {
            event.target.classList.toggle('picked')
            if (event.target.classList.contains('picked')) {
                this.selectedTime.push(value)
            }
            else {
                const index = this.selectedTime.indexOf(value);
                if (index > -1) {
                    this.selectedTime.splice(index, 1);
                }
            }
            this.selectedTime.sort((a,b) => {
                var aAry = a.split('')
                var bAry = b.split('')
                aAry[0] = this.dayRule[aAry[0]]
                bAry[0] = this.dayRule[bAry[0]]
                if(aAry[0] == bAry[0]){
                    return this.timeRule[aAry[1]]-this.timeRule[bAry[1]]
                }
                else{
                    return aAry[0]-bAry[0]
                }
            })
            this.mousepop = true
            const timeString = this.selectedTime.join(',')
            this.$emit('update:modelValue', {available_time: timeString, semester_label: this.semesterLabel})
        },
        changeLabel() {
            const timeString = this.selectedTime.join(',')
            this.$emit('update:modelValue', {available_time: timeString, semester_label: this.semesterLabel})
        }
    }
}
</script>
<style lang="sass" scoped>
@import "@/style/common.sass"
.labelInput
    height: 1.2rem
    width: 50%
    border: 2px solid #409EFF
    margin: 0 10px 10px 0
    border-radius: 4px
.assignTimePicker
    background-color: #fff
.picker-row
    border-bottom: 1px solid $LightBorder
    text-align: center
    user-select: none
.picker-col-lable
    word-wrap: break-word
    text-align: center
    padding: 3px
.picker-col
    word-wrap: break-word
    border-left: 1px solid $LightBorder
    text-align: center
    padding: 3px
.pickable
    cursor: pointer
    transition: 0.4s
    &:hover
        background-color: $BaseBorder
        opacity: 0.3
    &:active
        opacity: 1
.picked
    background-color: $BaseBorder
    opacity: 1
    &:hover
        opacity: 0.9
.row-border
    border-bottom: 2px solid #F2A54B
</style>
